import { HttpClient, NonNullablePaths } from '@wix/sdk-types';
import {
  Configuration,
  DisconnectAccountOptions,
  ListConfigurationsResponse,
  disconnectAccount as universalDisconnectAccount,
  disconnectConfiguration as universalDisconnectConfiguration,
  getConfiguration as universalGetConfiguration,
  listConfigurations as universalListConfigurations,
} from './restaurants-postpaapi-v1-configuration-pos-tpa.universal.js';

export const __metadata = { PACKAGE_NAME: '@wix/restaurants' };

/** @internal */
export function getConfiguration(
  httpClient: HttpClient
): GetConfigurationSignature {
  return (_id: string) =>
    universalGetConfiguration(
      _id,
      // @ts-ignore
      { httpClient }
    );
}

interface GetConfigurationSignature {
  /**
   * This endpoint gets a location configuration by ID on the Wix site.
   * @param - Configuration ID.
   * @returns The merchant's location configuration on Wix.
   */
  (_id: string): Promise<
    NonNullablePaths<
      Configuration,
      `location.name` | `catalogId` | `archived` | `enabled`,
      3
    >
  >;
}

/** @internal */
export function listConfigurations(
  httpClient: HttpClient
): ListConfigurationsSignature {
  return () =>
    universalListConfigurations(
      // @ts-ignore
      { httpClient }
    );
}

interface ListConfigurationsSignature {
  /**
   * This endpoint retrieves a list of location configurations on the Wix site.
   */
  (): Promise<
    NonNullablePaths<
      ListConfigurationsResponse,
      | `configurations`
      | `configurations.${number}.location.name`
      | `configurations.${number}.catalogId`
      | `configurations.${number}.archived`
      | `configurations.${number}.enabled`,
      5
    >
  >;
}

/** @internal */
export function disconnectAccount(
  httpClient: HttpClient
): DisconnectAccountSignature {
  return (accountId: string, options?: DisconnectAccountOptions) =>
    universalDisconnectAccount(
      accountId,
      options,
      // @ts-ignore
      { httpClient }
    );
}

interface DisconnectAccountSignature {
  /**
   * This endpoint disconnects the account between the merchant and the POS service provider.
   *
   * The account is not deleted. You can reconnect to the account using the POS integration settings in the Dashboard.
   *
   * Call the [List Account IDs](https://dev.wix.com/api/rest/wix-restaurants/point-of-sale-integration/pos-connectivity-spi/list-account-ids) SPI endpoint to retrieve a list of existing account IDs.
   * @param - The ID for the account between the merchant and the POS service provider.
   */
  (accountId: string, options?: DisconnectAccountOptions): Promise<void>;
}

/** @internal */
export function disconnectConfiguration(
  httpClient: HttpClient
): DisconnectConfigurationSignature {
  return (configurationId: string) =>
    universalDisconnectConfiguration(
      configurationId,
      // @ts-ignore
      { httpClient }
    );
}

interface DisconnectConfigurationSignature {
  /**
   * This endpoint disconnects the Wix location configuration from a POS service provider's location.
   *
   * The configuration is not deleted. You can reconnect to the configuration using the POS integration settings in the Dashboard.
   *
   * Call the [List Configurations](https://dev.wix.com/api/rest/wix-restaurants/point-of-sale-integration/pos-connectivity-api/list-configurations) SPI endpoint to retrieve a list of existing configuration IDs.
   * @param - The ID of the merchant's location configuration on Wix.
   */
  (configurationId: string): Promise<void>;
}

export {
  Configuration,
  DisconnectAccountOptions,
  DisconnectAccountRequest,
  DisconnectAccountResponse,
  DisconnectConfigurationRequest,
  DisconnectConfigurationResponse,
  GetConfigurationRequest,
  GetConfigurationResponse,
  ListConfigurationsRequest,
  ListConfigurationsResponse,
  Location,
} from './restaurants-postpaapi-v1-configuration-pos-tpa.universal.js';
